import React from 'react'
import OSFLogo from '../../images/OSFLogo.png'
import { Heading, Subtitle, TextWrapper, TopLine } from '../InfoSection/InfoElements'
import {IframeMobileContainer, IframeMobileWrapper, IframeMobileRow, IframeWrap, Column1, Column2, Logo} from './IframeMobileElemsnts'
import { ButtonScroll } from '../HeroSection/HeroElements'
import card1 from '../../images/OSFBackground.png'

const IframeMobile = () => {
    return (
        <>
            <IframeMobileContainer style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 0%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 10%), url(${card1})`}}>
                <IframeMobileWrapper>
                    <IframeMobileRow>
                        <Column1>
                            <TextWrapper>
                                <Logo src={OSFLogo}/>
                                <Heading style={{color: '#01bf71'}}>OSF Posture Application</Heading>
                                <Subtitle>This project was a collaboration of which I had the opportunity of working with a physical therapist at OSF HealthCare. I and two other classmates spent ten weeks designing in Figma and user testing this application with medical professionals and medical students.</Subtitle>
                                <Subtitle>After we had a design that was greenlit by our client, I alone was tasked with prototyping the application using Bubble.io for the remaining four weeks of the semester. I have included on this page a working Bubble.io prototype of what came to be called Upright Me.</Subtitle>
                                <ButtonScroll to="designs" smooth={true} duration={500} spy={true} exact='true' offset={-80} primary="true" dark="true">Learn more</ButtonScroll>
                            </TextWrapper>
                        </Column1>
                        <Column2>
                            <IframeWrap>
                                <TopLine style={{paddingLeft: '15px'}}>Working Prototype</TopLine>
                                <iframe src="https://osf-posture.bubbleapps.io/version-test/" loading="eager" scrolling="no" style={{width: '375px', height: '810px', border: 'none', borderRadius: '2pc'}}></iframe>
                            </IframeWrap>
                        </Column2>
                    </IframeMobileRow>
                </IframeMobileWrapper>  
            </IframeMobileContainer>  
        </>
    )
}

export default IframeMobile
