import React from 'react'
import CatLogo from '../../images/CatLogo.png'
import { Heading, Img, ImgWrap, Subtitle, TextWrapper, TopLine } from '../InfoSection/InfoElements'
import {IframeMobileContainer, IframeMobileWrapper, IframeMobileRow, IframeWrap, Logo} from '../IframeMobile/IframeMobileElemsnts'
import { ButtonScroll } from '../HeroSection/HeroElements'
import card1 from '../../images/CatImages/Dash1.png'
import card2 from '../../images/CatImages/IMG_5905.png'
import behind from '../../images/CatImages/CatMocks.png'
import { Column1, Column2 } from './CatHeroElements'

const CatHero = () => {
    return (
        <>
            <IframeMobileContainer style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 0%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 10%), url(${behind})`}}>
                <IframeMobileWrapper>
                    <IframeMobileRow>
                        <Column1>
                            <TextWrapper style={{paddingBottom: '25px'}}>
                                <Logo src={CatLogo}/>
                                <Heading style={{color: '#01bf71'}}>Caterpillar Design Challenge</Heading>
                                <Subtitle style={{marginBottom: '20px'}}>The Caterpillar Design Challenge was an opportunity where I was asked to help redesign Caterpillars dealership website. This challenge had two parts. PART ONE entailed looking at the original design and freshen it up.</Subtitle>
                            </TextWrapper>
                            <ImgWrap>
                                <Img src={card1}/>
                            </ImgWrap>
                        </Column1>
                        <Column2>
                            <ImgWrap>
                                <Img src={card2}/>
                            </ImgWrap>
                            <Subtitle style={{marginBottom: '10px'}}>In PART TWO of the challenge, I and the rest of the User Experience Design students from Bradley University were invited to Caterpillar Headquarters in Peoria, IL to work with their UX team on a hypothetical 100 floor building elevator design.</Subtitle>
                            <ButtonScroll to="partOne" smooth={true} duration={500} spy={true} exact='true' offset={-80} primary="true" dark="true">Learn more about PART ONE</ButtonScroll>
                        </Column2>
                    </IframeMobileRow>
                </IframeMobileWrapper>  
            </IframeMobileContainer>  
        </>
    )
}

export default CatHero