import styled from 'styled-components'

export const BackHeaderContainer = styled.div `
    background: ${({scrollNav}) => (scrollNav ? '#101521' : 'transparent')};
    height: 80px;
    margin-top: -80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: sticky;
    top: 0;
    z-index: 10;
    transition: 0.2s all ease;

    @media screen and (max-width: 480px) {
        height: ${({scrollNav}) => (scrollNav ? '' : '100px')};
        margin-top: -100px;
    }
`

export const BackHeaderWrapper = styled.div `
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 15px;
    max-width: 1100px;
    align-items: center;
    // margin-left: 24px;
    // padding-left: 24px;

     @media screen and (max-width: 480px) {
        padding: 15px;
    }
`

export const BreadcrumbsWrap = styled.div `
    margin-left: 24px;

    @media screen and (max-width: 480px) {
        margin-left: 0;
    }
`