import React from 'react'
import {Button} from '../ButtonElement'
import CatPartOneImageCarousel from '../CatPartOneCarousel'
import { Column1, Column2, ImgWrap, InfoContainer, InfoWrapper, InfoRow, TextWrapper, TopLine, Heading, Subtitle, BtnWrap, Img } from './CatInfoElements'
import { ButtonScroll } from '../HeroSection/HeroElements'

const CatInfoSection = ({lightBg, id, imgStart, topLine, lightText, headLine, darkText, description, buttonLabel, img, alt, primary, dark, dark2, toPage}) => {
    return (
        <>
            <InfoContainer lightBg={lightBg} id={id}>
                <InfoWrapper>
                    <InfoRow imgStart={imgStart}>
                        <Column1>
                        <TextWrapper>
                            <TopLine>{topLine}</TopLine>
                            <Heading lightText={lightText}>{headLine}</Heading>
                            <Subtitle darkText={darkText}>{description}</Subtitle>
                            <BtnWrap>
                            <ButtonScroll to="partTwo" smooth={true} duration={500} spy={true} exact='true' offset={-95} primary="true" dark="true">Learn more about PART TWO</ButtonScroll>
                            </BtnWrap>
                        </TextWrapper>
                        </Column1>
                        <Column2>
                            <CatPartOneImageCarousel />
                        </Column2>
                    </InfoRow>
                </InfoWrapper>
            </InfoContainer>  
        </>
    )
}

export default CatInfoSection
