import React, {useState, useEffect, useRef} from 'react'
import CarouselCard from './Card'
import {ImageCarouselContainer, Img, ArrowForward, ArrowBackward, CardWrapper, ImageCarouselWrapper, WidthHolder} from './CardElements'
import {FaArrowAltCircleRight, FaArrowAltCircleLeft} from 'react-icons/fa'
import { carouselOne, carouselThree, carouselTwo, carouselFour, carouselFive, carouselSix, carouselSeven, carouselEight, carouselNine, carouselTen, carouselEleven, carouselTwelve } from './Data'
import { TopLine } from '../InfoSection/InfoElements'

const ImageCarousel = ({slides}) => {
    const myRef = React.createRef();

    const prevClick = () => {
        const slide = myRef.current;
        slide.scrollLeft -= slide.offsetWidth;
        if(slide.scrollLeft <= 0) {
            slide.scrollLeft = slide.scrollWidth;
        }
    };

    const nextClick = () => {
        const slide = myRef.current;
        slide.scrollLeft += slide.offsetWidth;
        if(slide.scrollLeft >= (slide.scrollWidth - slide.offsetWidth)) {
            slide.scrollLeft = 0;
        }
    };

    return (
        <ImageCarouselContainer id="designs">
            <WidthHolder>
                <TopLine>FINAL DESIGNS</TopLine>
            </WidthHolder>
            <ImageCarouselWrapper>    
                <ArrowBackward onClick={prevClick}/>
                    <CardWrapper ref={myRef}>
                        <CarouselCard {...carouselOne}/>
                        <CarouselCard {...carouselTwo}/>
                        <CarouselCard {...carouselThree}/>
                        <CarouselCard {...carouselFour}/>
                        <CarouselCard {...carouselFive}/>
                        <CarouselCard {...carouselSix}/>
                        <CarouselCard {...carouselSeven}/>
                        <CarouselCard {...carouselEight}/>
                        <CarouselCard {...carouselNine}/>
                        <CarouselCard {...carouselTen}/>
                        <CarouselCard {...carouselEleven}/>
                        <CarouselCard {...carouselTwelve}/>
                    </CardWrapper>
                <ArrowForward onClick={nextClick}/>
            </ImageCarouselWrapper>
        </ImageCarouselContainer>
    )
}

export default ImageCarousel
