import React, {useState} from 'react'
import {HeroContainer, HeroBg, VideoBg, HeroContent, HeroH1, HeroP, HeroBtnWrapper, ArrowForward, ArrowRight, ButtonScroll} from './HeroElements'
import Video from '../../videos/Goose5.mp4'

const HeroSection = () => {
    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }

    return (
        <HeroContainer>
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='video/mp4'/>
            </HeroBg>
            <HeroContent>
                <HeroH1>Hi, I'm Jason</HeroH1>
                <HeroP>
                    I am a UX designer who likes front-end development... and geese
                </HeroP>
                <HeroP>
                    (Website work in progress)
                </HeroP>
                <HeroBtnWrapper>
                    <ButtonScroll to="about" smooth={true} duration={500} spy={true} exact='true' offset={-80} onMouseEnter={onHover} onMouseLeave={onHover} primary="true" dark="true">
                        To the stuff {hover ? <ArrowForward /> : <ArrowRight />}
                    </ButtonScroll>
                </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection
