import React from 'react'
import { FaFacebook, FaInstagram, FaLinkedin, FaTwitter } from 'react-icons/fa'
import {FooterContainer, FooterWrap, FooterLinksContainer, FooterLinksWrapper, FooterLinkItems, FooterLinkTitle, FooterLink, SocialMedia, SocialMediaWrap, SocialLogo, SocialIcons, SocialIconLink, WebsiteRights} from './FooterElements'

const Footer = () => {
    return (
        <>
            <FooterContainer>
                <FooterWrap>
                    <FooterLinksContainer>
                        <FooterLinksWrapper>
                            <FooterLinkItems>
                                <FooterLinkTitle>Links</FooterLinkTitle>
                                <FooterLink to="/about">About</FooterLink>
                                <FooterLink to="/portfolio">Portfolio</FooterLink>
                                <FooterLink to="/resume">Resume</FooterLink>
                                <FooterLink to="/contactme">Contact Me</FooterLink>                  
                            </FooterLinkItems>
                        </FooterLinksWrapper>
                    </FooterLinksContainer> 
                    <SocialMedia>
                        <SocialMediaWrap>
                            <SocialLogo to='/'>Bessett</SocialLogo>
                            <WebsiteRights>iAreGoose {new Date().getUTCFullYear()}</WebsiteRights>
                            <WebsiteRights>Built with React JS</WebsiteRights>
                            <SocialIcons>
                                <SocialIconLink href="//www.linkedin.com/in/jasonb-design/" target="_blank" aria_label="Linkedin">
                                    <FaLinkedin/>
                                </SocialIconLink>
                                <SocialIconLink href="//www.twitter.com/jason_bessett" target="_blank" aria_label="Twitter">
                                    <FaTwitter />
                                </SocialIconLink>
                                <SocialIconLink href="//www.instagram.com/j.bessett/" target="_blank" aria_label="Instagram">
                                    <FaInstagram />
                                </SocialIconLink>
                                <SocialIconLink href="//www.facebook.com/jason.bessett/" target="_blank" aria_label="Facebook">
                                    <FaFacebook />
                                </SocialIconLink>
                            </SocialIcons>
                        </SocialMediaWrap>    
                    </SocialMedia>   
                </FooterWrap>  
            </FooterContainer>  
        </>
    )
}

export default Footer
