import styled from 'styled-components'
import { MdArrowForward, MdKeyboardArrowRight, MdKeyboardArrowLeft } from 'react-icons/md'

export const Card = styled.div `
    min-width: 25%;
    overflow: hidden;
    margin: auto;

    @media screen and (max-width: 900px) {
        min-width: 33.333333%;
    }

    @media screen and (max-width: 480px) {
        min-width: 100%;
    }
`

export const ImageCarouselContainer = styled.div `
    padding: 50px;

    @media screen and (max-width: 480px) {
        padding: 50px 15px;
    }
`

export const ImageCarouselWrapper = styled.div `
    max-width: 1000px;
    width: 100%;
    min-height: 300px;
    position: relative;
    margin: auto;
    display: flex;
`

export const CardWrapper = styled.div `
    width: 100%;
    display: flex;
    overflow: hidden;
    scroll-behavior: smooth;
`

export const Img = styled.img `
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    border-radius: 10% 10% / 5% 5%;
    padding: 5px;
`

export const ArrowForward = styled(MdKeyboardArrowRight)
`
    font-size: 3rem;
    color: #fff;
    cursor: pointer;
    margin-top: auto;
    margin-bottom: auto;
`

export const ArrowBackward = styled(MdKeyboardArrowLeft)
`
    font-size: 3rem;
    color: #fff;
    cursor: pointer;
    margin-top: auto;
    margin-bottom: auto;
`

export const WidthHolder = styled.div `
    max-width: 1000px;
    margin: auto;
    padding-left: 35px;
    
    @media screen and (max-width: 480px) {
        padding-left: 0;
    }
`