import React from 'react'
import CatLogo from '../../images/7summitsLogoMix.png'
import { Heading, Img, Subtitle, TopLine } from '../InfoSection/InfoElements'
import {IframeMobileContainer, IframeMobileWrapper, IframeMobileRow, IframeWrap} from '../IframeMobile/IframeMobileElemsnts'
import { ButtonScroll } from '../HeroSection/HeroElements'
import card1 from '../../images/7Summits/SS1.png'
import card2 from '../../images/7Summits/SS2.png'
import behind from '../../images/7Summits/7SummitsBehind.png'
import { Column1, Column2, ImgWrap, Logo, TextWrapper } from './SSummitsHeroElements'

const SSummitsHero = () => {
    return (
        <>
           <IframeMobileContainer style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 0%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 10%), url(${behind})`}}>
                <IframeMobileWrapper>
                    <IframeMobileRow>
                        <Column1>
                            <TextWrapper >
                                <Logo src={CatLogo}/>
                            </TextWrapper>
                        </Column1>
                        <Column2>
                            <Heading style={{color: '#01bf71'}}>7Summits Internship</Heading>
                            <Subtitle style={{marginBottom: '20px'}}>During my internship, I Worked closely with the lightning web component development team as a front end developer, creating multiple web components. This was where my coding skills were put to the test for the first time in a real world working environment.</Subtitle>
                            {/* <Subtitle style={{marginBottom: '10px'}}>In PART TWO of the challenge, I and the rest of the User Experience Design students from Bradley University were invited to Caterpillar Headquarters in Peoria, IL to work with their UX team on a hypothetical 100 floor building elevator design.</Subtitle> */}
                            <ButtonScroll to="lwcComps" smooth={true} duration={500} spy={true} exact='true' offset={-80} primary="true" dark="true">View published LWC components</ButtonScroll>
                        </Column2>
                    </IframeMobileRow>
                    <ImgWrap>
                        <Img src={card1}/>
                    </ImgWrap>
                </IframeMobileWrapper>  
            </IframeMobileContainer>   
        </>
    )
}

export default SSummitsHero
