import './App.css';
import {HashRouter as Router, Route, Switch} from 'react-router-dom'
import Home from './pages';
import About from './pages/about';
import Portfolio from './pages/portfolio';
import Osf from './pages/osf';
import Cat from './pages/cat';
import Milieu from './pages/milieu';
import SSummits from './pages/ssummits';
import Loaf from './pages/loaf';
import OtherProjects from './pages/other';
import ScrollToTop from './components/ScrollToTop';

function App() {
  return (
    <Router>
      <ScrollToTop />
      <Switch>
        <Route path="/" component={Home} exact />
        <Route path="/about" component={About} exact />
        <Route path="/portfolio" component={Portfolio}  />
        <Route path="/osf_posture" component={Osf} exact />
        <Route path="/cat_design_challenge" component={Cat} exact />
        <Route path="/milieu_fermentation_project" component={Milieu} exact />
        <Route path="/7summits_internship" component={SSummits} exact />
        <Route path="/loaf" component={Loaf} exact />
        <Route path="/other_projects" component={OtherProjects} exact />
      </Switch>
    </Router>
  );
}

export default App;
