import React, {useState} from 'react';
import BackHeader from '../components/BackHeader';
import CatHero from '../components/CatHero';
import CatInfoSection from '../components/CatInfoSection';
import InfoSectionSlimmer from '../components/InfoSectionSlimmer';
import { homeObjOne, homeObjTwo } from '../components/CatInfoSection/Data';
import Footer from '../components/Footer';
import {Spacer} from '../components/SpacerElements';

const Cat = () => {
    return (
        <>
            <BackHeader />
            <CatHero />
            <CatInfoSection {...homeObjOne}/>
            <InfoSectionSlimmer {...homeObjTwo}/>
            <Footer />
        </>
    )
}

export default Cat
