import styled from 'styled-components'

export const IframeMobileContainer = styled.div `
    padding: 80px 50px 50px 50px ;

    @media screen and (max-width: 480px) {
        padding: 100px 0 0 0;
    }
`

export const IframeMobileWrapper = styled.div `
    display: grid;
    z-index: 1;
    height: 100%;
    width: 100%;
    max-width: 1000px;
    margin-right: auto;
    margin-left: auto;
    justify-content: center;

    padding-left: 20px;
    padding-top: 15px;
    padding-right: 20px;

    border-radius: 25px;

    backdrop-filter: blur(15px);
    background-color: #01060625;

    @media screen and (max-width: 480px) {
        height: 100%;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        padding: 0;
        overflow: hidden;
    }
`

export const IframeMobileRow = styled.div `
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    align-items: center;
    grid-template-areas: ${({imgStart}) => (imgStart ? `
'col2 col1'
` : `
'col1 col2'
`)};

    @media screen and (max-width: 786px) {
        grid-template-areas: ${({imgStart}) => (imgStart ? `
'col1'
'col2'
` : `
'col1 col1'
'col2 col2'
`)};
    }
`

export const IframeWrap = styled.div `
    width: 375px;
    margin-right: auto;
    margin-left: auto;
`

export const Column1 = styled.div `
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area: col1;  
`

export const Column2 = styled.div `
    margin-bottom: 15px;
    // padding: 0 15px;
    grid-area: col2;
`

export const Logo = styled.img `
    height: 200px;
    width: 200px;
    margin-bottom: 10px;
`