import React from 'react';
import {SidebarContainer, CloseIcon, Icon, SidebarWrapper, SidebarLink, SideBtnWrap, SidebarRoute, SidebarMenu } from './SidebarElements';

const Sidebar = ({isOpen, toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLink to="about" smooth={true} duration={500} spy={true} exact='true' offset={-80} onClick={toggle}>
                        About
                    </SidebarLink>
                    <SidebarLink to="portfolioGal" smooth={true} duration={500} spy={true} exact='true' offset={-80} onClick={toggle}>
                        Portfolio
                    </SidebarLink>
                    <SidebarLink to="resume" smooth={true} duration={500} spy={true} exact='true' offset={-80} onClick={toggle}>
                        Resume
                    </SidebarLink>
                    <SidebarLink to="contact" smooth={true} duration={500} spy={true} exact='true' offset={-80} onClick={toggle}>
                        Contact Me
                    </SidebarLink>
                </SidebarMenu>
                {/* <SideBtnWrap>
                    <SidebarRoute to='/signin'>Sign In</SidebarRoute>
                </SideBtnWrap> */}
            </SidebarWrapper>
        </SidebarContainer>
    )
}

export default Sidebar
