import React from 'react'
import CarouselCard from './Card'
import {ArrowForward, ArrowBackward, CardWrapper, ImageCarouselWrapper, WidthHolder} from './CardElements'
import { carouselOne, carouselThree, carouselTwo} from './Data'

const CatPartOneImageCarousel = ({slides}) => {
    const myRef = React.createRef();

    const prevClick = () => {
        const slide = myRef.current;
        slide.scrollLeft -= slide.offsetWidth;
        if(slide.scrollLeft <= 0) {
            slide.scrollLeft = slide.scrollWidth;
        }
    };

    const nextClick = () => {
        const slide = myRef.current;
        slide.scrollLeft += slide.offsetWidth;
        if(slide.scrollLeft >= (slide.scrollWidth - slide.offsetWidth)) {
            slide.scrollLeft = 0;
        }
    };

    return (
            <ImageCarouselWrapper>    
                <ArrowBackward onClick={prevClick}/>
                    <CardWrapper ref={myRef}>
                        <CarouselCard {...carouselOne}/>
                        <CarouselCard {...carouselTwo}/>
                        <CarouselCard {...carouselThree}/>
                    </CardWrapper>
                <ArrowForward onClick={nextClick}/>
            </ImageCarouselWrapper>
    )
}

export default CatPartOneImageCarousel
