import React, {useState} from 'react';
import BackHeader from '../components/BackHeader';
import Footer from '../components/Footer';
import {Spacer} from '../components/SpacerElements';
import SSummitsHero from '../components/SSummitsHero/SSummitsHero';
import InfoSectionSlimmerButton from '../components/InfoSectionSlimmerButton';
import { homeObjOne, homeObjTwo, homeObjThree } from '../components/SSummitsHero/Data';

const SSummits = () => {
    return (
        <>
            <BackHeader />
            <SSummitsHero />
            <InfoSectionSlimmerButton {...homeObjOne}/>
            <InfoSectionSlimmerButton {...homeObjTwo}/>
            <InfoSectionSlimmerButton {...homeObjThree}/>
            <Footer />
        </>
    )
}

export default SSummits