import React, {useState} from 'react'
import Icon1 from '../../images/OSFLogo.png'
import Icon2 from '../../images/CatLogo.png'
import Icon3 from '../../images/miliueLogo.png'
import Icon4 from '../../images/7summitsLogoMix.png'
import Icon5 from '../../images/LoafLogo.png'
import Icon6 from '../../images/VR-WatchLogo.png'
import {PortfolioGalContainer, PortfolioGalH1, PortfolioGalWrapper, PortfolioGalCard, PortfolioGalIcon, PortfolioGalH2, PortfolioGalP, PortfolioWrap, PortfolioTopline} from './PortfolioGalElements'
import card1 from '../../images/OSFBackground.png'
import card2 from '../../images/CatBackground.jpg'
import card3 from '../../images/milieuBackground.png'
import card4 from '../../images/7SummitsBackground.png'
import card5 from '../../images/LoafBackground.png'
import card6 from '../../images/OtherProjectsBackground.png'


const PortfolioGal = () => {
    const [isOpen , setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen);
    };

    return (
        <>
           <PortfolioGalContainer id='portfolioGal'>
               <PortfolioWrap>
                <PortfolioTopline>Portfolio</PortfolioTopline>
                <PortfolioGalH1>I made some stuff</PortfolioGalH1>
                <PortfolioGalWrapper>
                    <PortfolioGalCard to="/osf_posture" style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%), url(${card1})`}}>
                        <PortfolioGalIcon src={Icon1}/>
                        <PortfolioGalH2>OFS Posture App</PortfolioGalH2>
                    </PortfolioGalCard>
                    <PortfolioGalCard to="/cat_design_challenge" style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%), url(${card2})`}}>
                        <PortfolioGalIcon src={Icon2}/>
                        <PortfolioGalH2>Caterpillar Design Challenge</PortfolioGalH2>
                    </PortfolioGalCard>
                    <PortfolioGalCard to="/7summits_internship" style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%), url(${card4})`}}>
                        <PortfolioGalIcon src={Icon4}/>
                        <PortfolioGalH2>7Summits Internship</PortfolioGalH2>
                    </PortfolioGalCard>
                    <PortfolioGalCard to="/milieu_fermentation_project" style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%), url(${card3})`}}>
                        <PortfolioGalIcon src={Icon3}/>
                        <PortfolioGalH2>&#91;milieu&#93; Fermentation Project</PortfolioGalH2>
                    </PortfolioGalCard>
                    <PortfolioGalCard to="/loaf" style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%), url(${card5})`}}>
                        <PortfolioGalIcon src={Icon5}/>
                        <PortfolioGalH2>LOAF</PortfolioGalH2>
                    </PortfolioGalCard>
                    <PortfolioGalCard to="/other_projects" style={{backgroundImage: `linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.6) 100%), linear-gradient(180deg, rgba(0,0,0,0.2) 0%, transparent 100%), url(${card6})`}}>
                        <PortfolioGalIcon src={Icon6}/>
                        <PortfolioGalH2>Other Projects</PortfolioGalH2>
                    </PortfolioGalCard>
                </PortfolioGalWrapper>
               </PortfolioWrap>
            </PortfolioGalContainer> 
        </>
    )
}

export default PortfolioGal
