import styled from 'styled-components'

export const ContactFormContainer = styled.div `
    color: #fff;
    background: #01bf71;
    padding: 50px;

    @media screen and (max-width: 480px) {
        padding: 50px 15px;
    }
`

export const ContactFormWrapper = styled.div `
    display: grid;
    height: 100%;
    width: 100%;
    max-width: 1000px;
    margin-right: auto;
    margin-left: auto;
    justify-content: center;
`

export const Form = styled.div `
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    align-items: center;
    grid-template-areas: 'name phone message message' 'email email message message' 'clear send message message';

    @media screen and (max-width: 480px) {
        grid-template-areas: 'name phone' 'email email' 'message message' 'message message' 'clear send';
    }
`

export const FirstPhone = styled.div `
    display: grid;
    // grid-auto-columns: minmax(auto, 1fr);
    // align-items: center;
    // grid-template-areas: 'col1 col2';
    // grid-area: col1;
    // margin-bottom: 25px;

    @media screen and (max-width: 786px) {
        grid-template-areas: ${({imgStart}) => (imgStart ? `'col1''col2'` : `'col1 col1''col2 col2'`)};
    }
`

export const Label = styled.label `

`

export const Input = styled.input `
    border-radius: 3px;
    border: none;
    padding: 0.5em;

    &:focus {
        outline: none;
    }
`

export const InputMessage = styled.textarea `
    resize: none;
    border-radius: 3px;
    border: none;
    padding: 0.5em;

    &:focus {
        outline: none;
    }
`

export const First = styled.div `
    margin-bottom: 15px;
    padding-right: 15px;
    grid-area: name;

    @media screen and (max-width: 480px) {
        padding-right: 7.5px;
    }
`

export const Phone = styled.div `
    margin-bottom: 15px;
    padding-right: 15px;
    grid-area: phone;

    @media screen and (max-width: 480px) {
        padding-left: 7.5px;
        padding-right: 0px;
    }
`

export const Email = styled.div `
    display: grid;
    margin-bottom: 15px;
    grid-area: email;
    padding-right: 15px;

    @media screen and (max-width: 480px) {
        padding: 0;
    }
`

export const Message = styled.div `
    display: grid;
    grid-area: message;
    padding-left: 15px;

    @media screen and (max-width: 480px) {
        padding: 0;
    }
`