import React, {useState, useEffect} from 'react';
import Breadcrumbs from '../Breadcrumbs'
import {BackHeaderContainer, BackHeaderWrapper, BreadcrumbsWrap} from './BackHeaderElements'

const BackHeader = () => {
    const [scrollNav, setScrollNav] = useState(false)

    const changeNav = ()=> {
        if(window.scrollY >= 80) {
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, []);

    return (
        <>
            <BackHeaderContainer scrollNav={scrollNav}>
                <BackHeaderWrapper>
                    <BreadcrumbsWrap>
                        <Breadcrumbs />
                    </BreadcrumbsWrap>
                </BackHeaderWrapper>
            </BackHeaderContainer>
            
        </>
    )
}

export default BackHeader
